export default {
	data() {
		return {
			username:"",
			password:"",
			repassword:""
		}
	},
	methods: {
		jumpUrl(){
			uni.navigateBack({
				delta:1
			})
		},
		register(){
			if(this.username.length==0){
				this.showMessage("请输入用户名")
				return
			}
			if(this.password.length==0 || this.repassword.length==0){
				this.showMessage("请输入密码")
				return
			}
			if(this.password!=this.repassword){
				this.showMessage("两次密码不相同，请重新输入")
				return
			}
			uni.request({
				url:'https://www.wanandroid.com/user/register',
				method:'POST',
				header:{
					'Content-Type':'application/x-www-form-urlencoded'
				},
				dataType:"json",
				data:{
					username:this.username,
					password:this.password,
					repassword:this.repassword
				},
				success: (res) => {
					if(res.data.errorCode==-1){
						this.showMessage(res.data.errorMsg)
					}else{
						uni.showModal({
							title:'系统提示',
							content:'注册成功，是否立即登录？',
							success: (dialog) => {
								if(dialog.confirm){
									this.jumpUrl()
									// uni.setStorage({
									// 	key:'user_login',
									// 	data:JSON.stringify(res.data.data)
									// })
								}else if(dialog.cancel){
									this.showMessage("那就下次登录吧！")
								}
							}
						})
					}
					console.log(res)
				},
				fail: (error) => {
					this.showMessage(error.errMsg)
				}
			})
		},
		showMessage(title){
			uni.showToast({
				title:title,
				duration:2000,
				icon:'none'
			})
		}
	}
}
